import ModalSuccess from "../../components/Modals/SuccessModal.vue";
import WarningMessage from "../../components/Modals/WarningMessage.vue";
import DangerMessage from "../../components/Modals/DangerMessage.vue";
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
    components: { WarningMessage, DangerMessage, ModalSuccess },
    head: {
        title: "Регистрация — Tujjor. Низкие цены и широкий ассортимент!",
        meta: [
            {
                hid: "description",
                name: "description",
                content: "Регистрация - Tujjor"
            }
        ]
    },

    data() {
        return {
            passwordRepeat: "",
            user: {
                name: "",
                phone: "",
                password: "",
                email: ""
            },

            userData: {
                phone: "",
                password: "",
                pin: ""
            },

            pin: "",

            messageNoData: false,
            messageDuplicate: false,
            resendPin: false,
            pinSended: false,

            codeBox: false
        };
    },
    methods: {
        ...mapMutations(["updateLoggedIn"]),

        resetModals() {
            this.messageNoData = this.messageDuplicate = this.resendPin = this.pinSended = false;
        },

        async registerUserMainFunc() {
            this.resetModals();
            // console.log(
            //     "message start",
            //     this.messageNoData,
            //     this.messageDuplicate
            // );
            if (
                !!this.user.name &&
                !!this.user.phone &&
                !!this.user.password &&
                !!this.user.email
            ) {
                await this.registerUser();
            } else {
                this.$nextTick(() => {
                    this.messageNoData = true;
                    // console.log(
                    //     "message start",
                    //     this.messageNoData,
                    //     this.messageDuplicate
                    // );
                });
            }
        },

        async checkCode() {
            await this.$axios
                .$post("user/checkCode", {
                    phone: this.userData.phone,
                    code: this.pin
                })
                .then(res => {
                    if (!!res.success && !!res.token) {
                        this.loginUser();
                        return res;
                    } else {
                        throw new Error("Couldn't login in!");
                    }
                })
                .catch(err => console.error(err));
        },

        async sendPin(a) {
            this.resetModals();
            await this.$axios
                .$post("user/getCode", {
                    phone: this.userData.phone
                })
                .then(res => {
                    if (!!res.success && !!res.message) {
                        !!a ? (this.pinSended = true) : (this.resendPin = true);
                        console.log(res);
                        return res;
                    } else {
                        throw new Error("Couldn't send pin!");
                    }
                })
                .catch(err => {
                    console.error(err);
                });
        },

        async registerUser() {
            let phone = this.user.phone.replace(/[^0-9]/g, "");
            this.userData = { ...this.user };
            this.userData.phone = phone;
            const data = await this.$axios
                .$post("/user/create", this.userData)
                .then(res => {
                    if (!!res.token) {
                        return res;
                    } else if (!res.success && res.err.code === 11000) {
                        this.$nextTick(() => {
                            this.messageDuplicate = true;
                            // console.log(
                            //     "message start",
                            //     this.messageNoData,
                            //     this.messageDuplicate
                            // );
                        });
                        return res;
                    } else if (res.success && !!res.phone) {
                        this.codeBoxActive();
                        this.sendPin(true);
                        return res;
                    } else {
                        this.$nextTick(() => {
                            this.messageDuplicate = true;
                            // console.log(
                            //     "message start",
                            //     this.messageNoData,
                            //     this.messageDuplicate
                            // );
                        });
                        throw new Error("Couldn't save data!");
                    }
                })
                .catch(err => {
                    this.$nextTick(() => {
                        this.messageDuplicate = true;
                        // console.log(
                        //     "message start",
                        //     this.messageNoData,
                        //     this.messageDuplicate
                        // );
                    });
                    return err;
                });
            this.updateLoggedIn();
        },

        async loginUser() {
            try {
                let response = await this.$auth.loginWith("local", {
                    data: {
                        phone: this.userData.phone,
                        password: this.userData.password
                    }
                });
                this.$router.push("/");
                this.updateLoggedIn();
            } catch (err) {
                console.error(err);
            }
        },

        codeBoxActive() {
            this.codeBox = !this.codeBox;
        }
    }
};
